@use 'base/variables' as v;
@use 'base/mixins' as m;


.nav {
  background-color: v.$verde;
  padding: .5rem 0;


  &__border {
    padding: 1.5rem 0;
    border-block: 2px dashed v.$blanco;

  }

  &__contenedor {
    display: flex;
    flex-direction: column;
    align-items: center;
    
    @include m.tablet{
      flex-direction: row;
      justify-content: space-between;

    }
  }

  &__enlace{
    padding: .5rem 2rem;
    font-weight: bold;
    font-size: 2.3rem;
    color: v.$blanco;

    &:hover {
      background-color: v.$vOscuro;
      border-radius: 20px 0 20px 0;
    }
  }
}