@use 'base/mixins' as m;
@use 'base/variables' as v;

.footer {
  padding-top: 8rem;
  text-align: center;

  &__title {
    
  }


  &__grid {
    @include m.tablet {
      @include m.grid(3, 3rem);
    }
  }

  &__horario {
    text-align: center;
  }

  &__ubicacion {
    text-align: center;
  }

  &__section {
