@use 'base/mixins' as m;
@use 'base/variables' as v;


.productos{


  &__title {
    @include m.subtitle(v.$rosa)
  }

  &__span {
    @include m.span(v.$negro);
  }

  &__grid {

    @include m.tablet {
      align-items: center;
      @include m.grid (2, 2rem);
    }

    @include m.pc {
      align-items: center;
      @include m.grid (3, 2rem);
    }
  }
}


.producto {
  margin-bottom: 2rem;
  
  @include m.tablet {
    margin-bottom: 0;
  }

  @include m.pc {
    &:first-child{
      grid-column: 1/3;
    }

    &:last-child{
      grid-column: 2/4;
    }
  }

  &__title {
    color: v.$rosa;
  }


  &__mask {
    overflow: hidden;
   

  }
  &__img {
    box-shadow: 1px -4px 10px -3px v.$rosa;
    transition-duration: .3s;
    transition-property: transform;
  
    &:hover {
      transform: scale(1.1);
    }
   
  }

  &__info {
 
    transform: translateY(-2rem);
    padding: 1.5rem;
    background-color: v.$blanco;
    transition-duration: .3s;
    transition-property: transform;

    &:hover {
      transform: translatey(-5rem);
      }
  }

  &__text {
    padding: 0 1rem;
    text-align: justify;
  }

  &__precio {
    font-weight: bold;
    color: v.$gris;
    font-size: 3rem;
    text-align: center;
    margin:0;
  }
}

