@use 'variables' as v;


@mixin phone {
  @media(min-width: v.$phone){
    @content;
  }
}
@mixin tablet {
  @media(min-width: v.$tablet){
    @content;
  }
}

@mixin pc{
  @media(min-width: v.$pc){
    @content;
  }
}

@mixin monitor{
  @media(min-width: v.$monitor){
    @content;
  }
}


//display grid
@mixin grid($columnas,$gap){
  display:grid;
  grid-template-columns: repeat($columnas,1fr);
  gap:$gap;
}


@mixin redes ($enlace ){

  &::before {
    content: "";
    display: inline-block;
    padding: 2rem;
    background-image:url($enlace);
  }
 
}


@mixin subtitle($color) {
  color: $color;
  text-align: center;
  
}

@mixin span ($color) {
  color: $color;
  text-transform: uppercase;
  display: block;
  font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

@mixin btn ( $colorB, $colorT,$colorH){
  background-color: $colorB;
  color: $colorT;
  padding: 1rem 2rem;
  display: block;


  &:hover {
    background-color: $colorH;
  }
}