@use 'base/variables' as v;





.swiper {
  width: 100%;
  height: 100%;

  &-pagination-bullet-active{
    background-color: v.$verde;
  }


}

.swiper-slide {
  background-position: center;
  background-size: cover;
}

.swiper-slide img {
  display: block;
  width: 100%;
}