

@use 'base/mixins' as m;
@use 'base/variables' as v;


.header {


  &__contenido {
    display: flex;
    flex-direction: column;
    align-items: center;


    @include m.tablet {
      flex-direction: row;
      justify-content: space-between;
    }
  }


  &__icon {
    padding: 3rem 0;
    max-width: 30rem;
  }

  &__redes {
    justify-content: center;
    display: flex;
    
    @include m.tablet {
      padding: 0;

    }
  }

  &__enlace {
    padding: 2rem 0
  }

  &__enlace[href*="https://www.facebook.com"] {
    @include m.redes ("../img/icono_facebook.svg");
  }

  &__enlace[href*="https://twitter.com"] {
    @include m.redes ("../img/icono_twitter.svg");
  }

  &__enlace[href*="https://www.instagram.com"] {
    @include m.redes ("../img/icono_instagram.svg");
  }

  &__enlace[href*="https://www.youtube.com"] {
    @include m.redes ("../img/icono_youtube.svg");
  }

  &__enlace[href*="https://www.tiktok.com"] {
    @include m.redes ("../img/icono_tiktok.svg");
  }
}


