@use 'base/mixins' as m;
@use 'base/variables' as v;

.bienvenida {
  padding: 4rem 0;

  &__title {
    @include m.subtitle( v.$rosa);
  }

  &__span {
    @include m.span(v.$negro);
    
  }

  &__text {
    text-align: center;
    font-size: 2.5rem;
    color: v.$gris;
    width: 55%;
    margin: 0 auto;
  }
}

