@use 'base/mixins' as m;
@use 'base/variables' as v;


.cards {
  padding-bottom: 4rem;

  &__grid {
    
  @include m.tablet {
    @include m.grid(3, 2rem);
  }
  }
}

.card {


  &__img {
    border: 5px solid v.$blanco;
    transition-property: transform;
    transition-duration: .1s;
  
    &:hover {
      transform: scale(1.1)rotate(3deg);
    }
  }

  &__info {
    background-color: v.$blanco;
    width: 90%;
    margin: 0 auto;
    margin-top: -4rem;
    transform: translateY(-5rem);
   box-shadow: 1px -4px 10px -3px v.$rosa;;
  }

  &__title {
    padding: 1.5rem  1rem  0 1rem;
    font-size: 2.3rem;
    @include m.subtitle (v.$rosa);

  }

  &__span {
    @include m.span (v.$negro);
  }

  &__btn {
    @include m.btn (v.$rosa, v.$blanco, v.$rOscuro );
    font-weight: bold;
  }
}
