@use 'base/mixins' as m;
@use 'base/variables' as v;

.horarios {
  padding: 4rem 0;
  margin-bottom: 3rem;
  background-image: linear-gradient(to bottom, v.$vOscuro 50%, v.$vOscuro 55%,transparent 0%), url(../img/bg_horarios.jpg);
  background-position: center, center center;
  background-repeat: no-repeat;
  background-size: cover;


  @include m.tablet {
    background-image: linear-gradient(to right, v.$vOscuro 55%, transparent  55%), url(../img/bg_horarios.jpg);
  }

  &__grid {
    padding: 3rem 0;
  

    @include m.tablet {
      @include m.grid(2,0);
      grid-template-rows: unset;
    }
  }

  &__contenido {
    color: v.$blanco;
  
  
  }


}

.tabla {
  border-collapse: collapse;
  width: 100%;
  border: 0.3rem solid v.$rosaT ;
  background-color: v.$rosaT;

  &__thead {
    
  }

  &__tr {


    &:hover {
      background-color: v.$vOscuro;

    }

  }

  &__td, &__th{
    padding: 1rem 0;
  }



  &__tbody {
    background-color: v.$verdeT;
  }
}